<script>

import $ from 'jquery';
import 'jquery-ui';
import 'jquery-ui/ui/widgets/draggable';
import html2canvas from 'html2canvas';

export default {
    name: 'MenuGenerator',
    data(){
        return {
            canvas:{
                width: 1200,
                bgcolor: "#DFE8E0",
            },
            editState: 'general',
            albumUrl:'',
            album:'',
            canvasStyle: {
                width: '1200px',
            } ,
            items: [],
            tags: ["新刊", "既刊", "R18", "NEW", "無料", "量少"],
            
        }
    },
    methods: {
        debug(){
            this.items = [{
                imgUrl: "https://i.imgur.com/NljafsO.jpeg",
                name:"新刊",
                subTitle:"A5變形/NTD120",
                content:"很多唧唧的本子",
                },{
                imgUrl: "https://i.imgur.com/NljafsO.jpeg",
                name:"既刊刊",
                subTitle:"A5變形/NTD150",
                content:"很多便便的本子",
                },{
                imgUrl: "https://i.imgur.com/NljafsO.jpeg",
                name:"既刊刊",
                subTitle:"A5變形/NTD150",
                content:"很多便便的本子",
                // },{
                // imgUrl: "https://i.imgur.com/NljafsO.jpeg",
                // name:"既刊刊",
                // subTitle:"A5變形/NTD150",
                // content:"很多便便的本子",
                // },{
                // imgUrl: "https://i.imgur.com/QMhs8Vr.png",
                // name:"既刊刊",
                // subTitle:"A5變形/NTD150",
                // content:"很多便便的本子",
                // },{
                // imgUrl: "https://i.imgur.com/NljafsO.jpeg",
                // name:"既刊刊",
                // subTitle:"A5變形/NTD150",
                // content:"很多便便的本子",
                },
            ]
            var self = this;
            this.items.forEach(item =>{
                item.tags = [];
                for(let i=0; i<self.tags.length; i++){
                    item.tags.push(false);
                }
            });
        },
        showEdit(state){
            this.editState = state;
        },
        addTag(){
            if(!$('#inputCostumeTag').val())
                return;
            this.tags.push($('#inputCostumeTag').val());
            $('#inputCostumeTag').val(''); 
        },
        deleteTag(index){
            this.tags.splice(index, 1);
            this.items.forEach(item=>{
                item.tags.splice(index,1)
            })
        },
        addItem(){
            this.items.push({id:this.items.length,tags:[]});
            for(let i=0; i<this.tags.length; i++){
                    this.items[this.items.length-1].tags.push(false);
                }
            
        },
        deleteItem(index){
            this.items.splice(index, 1);
        },
        loadImgur(){
            var self = this;
            this.album = this.albumUrl.split('/').pop();
            $.ajax({
                url: 'https://api.imgur.com/3/album/' + this.album + '/images',
                type: "get",
                dataType: "json",
                headers: {
                    'Authorization': 'Client-ID e27e4520784387d'
                },
            }).done(function (res) {
                res.data.forEach(element => {
                    self.items.push({imgUrl: element.link})
                });
            });
            self.items.forEach(item =>{
                item.tags = Array(this.tags.length)
            })
            // https://imgur.com/a/8NaynSf
        },
        swapItem(a, b){
            if(b<0 || b == this.items.length)
                return;
            if(this.items.length == 1)
                return;
            this.items.splice(b, 1 ,this.items.splice(a, 1, this.items[b])[0]);
        },
        chooseColumn(num){
            var width = "span " + 12/num
            $(".good").css("grid-column", width);
        },
        chooseDirec(d){
            switch(d){
                case "row":
                    $(".good").css("flex-direction", "row");
                    $(".good__text").css("text-align", "left");
                    break;
                case "col":
                    $(".good").css("flex-direction", "column");
                    $(".good__text").css("text-align", "center");
                    break;
            }
        },
        getImage(){
            html2canvas(document.getElementById('canvas')).then(function(canvas) {
            document.body.appendChild(canvas);
            var dataUrl = canvas.toDataURL("image/png");
            var downloadUrl = dataUrl.replace("image/png","image/octet-stream");//圖片地址

            window.location.href = downloadUrl; // 跳轉下載
        });
        }

    },
    created(){
        // var self = this;
        this.debug();
    
    },
    watch:{
        "canvas.width"(){
            this.canvasStyle.width = this.canvas.width+"px";
        },
        "canvas.bgcolor"(){
            this.canvasStyle.backgroundColor = this.canvas.bgcolor;
        },
        tags(){
            this.items.forEach(item=>{
                while(item.tags.length < this.tags.length){
                    item.tags.push(false);
                }
            })
        }
        
    },
    updated() {
        // console.log(this.items[0].tags);
    },
}
$(function(){
    $("#edit-panel").draggable();
    
});

</script>

